
  import wepy from 'wepy'
  import GroupItem from './groupitem'

  export default class Group extends wepy.component {
    props = {
      grouplist: {},
      index: {}
    }

   $repeat = {"grouplist":{"com":"groupitem","props":"gitem"}};
$props = {"groupitem":{"xmlns:v-bind":{"value":"","for":"grouplist.list","item":"item","index":"index","key":"key"},"v-bind:gitem.once":{"value":"item","type":"item","for":"grouplist.list","item":"item","index":"index","key":"key"}}};
$events = {};
 components = {
      groupitem: GroupItem
    }
    methods = {
      tap () {
        this.grouplist.name = `Parent Random(${Math.random()})`
        console.log(`Clicked Group ${this.$index}, ID is ${this.grouplist.id}`)
      }
    }
  }
